import React from "react"
import { TextInput } from "./Input"
import { VertexData } from "./LinePlacementPlot"

interface TextFieldProps {
  coords: VertexData
  index: number
  setCoordinates: React.Dispatch<React.SetStateAction<VertexData[]>>
  setCoordinatesChanged: React.Dispatch<React.SetStateAction<boolean>>
  setIndex: React.Dispatch<React.SetStateAction<number | undefined>>
}

export const TextField: React.FC<TextFieldProps> = ({
  coords,
  index,
  setCoordinates,
  setCoordinatesChanged,
  setIndex,
}) => {
  const handleClick = () => {
    setIndex(undefined)
    setCoordinates((coords) => {
      const updatedCoords = [...coords]
      updatedCoords.splice(index, 1)
      setCoordinatesChanged(true)
      return updatedCoords
    })
  }

  const coordinateClick = (arg: string, whichProp: keyof VertexData): void => {
    if (!isNaN(+arg)) {
      const newCoord = +arg
      setCoordinates((coords) => {
        let coordRef: any = coords[index]
        coordRef[whichProp] = newCoord
        coords[index] = coordRef
        setCoordinatesChanged(true)
        return [...coords]
      })
    }
  }

  const textClick = (arg: string, whichProp: keyof VertexData): void => {
    setCoordinates((coords) => {
      const newVal = arg
      console.log(newVal)
      let coordRef: any = coords[index]
      coordRef[whichProp] = newVal
      coords[index] = coordRef
      setCoordinatesChanged(true)
      return [...coords]
    })
  }
  return (
    <div>
      <TextInput
        placeholder="Name"
        value={coords.name}
        whichProp={"name"}
        onClick={textClick}
      />
      <TextInput
        placeholder="Direction 1"
        value={coords.dir1}
        whichProp={"dir1"}
        onClick={textClick}
      />

      <TextInput
        placeholder="Direction 2"
        value={coords.dir2}
        whichProp={"dir2"}
        onClick={textClick}
      />
      <TextInput
        placeholder="First X-coordinate"
        value={String(coords.x1)}
        whichProp={"x1"}
        onClick={coordinateClick}
      />
      <TextInput
        placeholder="First Y-coordinate"
        value={String(coords.y1)}
        whichProp={"y1"}
        onClick={coordinateClick}
      />
      <TextInput
        placeholder="Second X-coordinate"
        value={String(coords.x2)}
        whichProp={"x2"}
        onClick={coordinateClick}
      />
      <TextInput
        placeholder="Second Y-coordinate"
        value={String(coords.y2)}
        whichProp={"y2"}
        onClick={coordinateClick}
      />
      <button
        onClick={handleClick}
        style={{
          backgroundColor: "#282434",
          outline: "none",
          padding: "10px",
          color: "#ffffff",
          borderRadius: "5px",
          border: "none",
          fontSize: "12px",
          margin: "4px",
          boxSizing: "border-box",
          width: "5%",
        }}
      >
        X
      </button>
    </div>
  )
}
