import React from "react"
import { VertexData } from "./LinePlacementPlot"

interface VertexProps {
  data: VertexData
  index: number
  color: string
  setIndex: React.Dispatch<React.SetStateAction<number | undefined>>
}

const RADIUS = 7

const Vertex: React.FC<VertexProps> = ({
  data: { x1, y1, x2, y2, name, dir1, dir2 },
  index,
  color,
  setIndex,
}) => {
  let length = Math.sqrt(Math.pow(x2 - x1, 2) + Math.pow(y2 - y1, 2))
  let dir: [number, number] = [(x2 - x1) / length, (y2 - y1) / length]
  let perpendicularDir: [number, number] = [-dir[1], dir[0]]
  let from = `${x1 + dir[0] * RADIUS},${y1 + dir[1] * RADIUS}`
  let to = `${x2},${y2}`

  let midX = (x1 + x2) / 2
  let midY = (y1 + y2) / 2

  const onClick = () => {
    setIndex(index)
  }
  return (
    <svg>
      <circle
        key={`${x1}${y1}`}
        cx={x1}
        cy={y1}
        r={`${RADIUS}`}
        stroke={color}
        strokeWidth="3"
        fill="none"
        onClick={onClick}
      />
      <circle
        key={`${x1}${y1}'`}
        cx={x1}
        cy={y1}
        r={`${RADIUS}`}
        stroke="transparent"
        strokeWidth="6"
        fill="transparent"
        onClick={onClick}
      />
      <polyline
        key={`${x2}${y2}`}
        points={`${from} ${to}`}
        stroke={color}
        strokeWidth="3"
        fill="none"
        onClick={onClick}
      />
      <polyline
        key={`${x2}${y2}'`}
        points={`${from} ${to}`}
        stroke="transparent"
        strokeWidth="6"
        fill="transparent"
        onClick={onClick}
      />
      <text
        x={x1 - name.length * 2}
        y={y1 - RADIUS * 2}
        fontSize={10}
        fontFamily="sans-serif"
        fill="white"
        onClick={onClick}
      >
        {name}
      </text>
      <text
        x={midX + perpendicularDir[0] * RADIUS * 2}
        y={midY + perpendicularDir[1] * RADIUS * 2}
        textAnchor="middle"
        fontSize={8}
        fontFamily="sans-serif"
        fill="white"
        onClick={onClick}
      >
        {dir1}
      </text>
      <text
        x={midX - perpendicularDir[0] * RADIUS * 2}
        y={midY - perpendicularDir[1] * RADIUS * 2}
        textAnchor="middle"
        fontSize={8}
        fontFamily="sans-serif"
        fill="white"
        onClick={onClick}
      >
        {dir2}
      </text>
    </svg>
  )
}

export default Vertex
