import { Streamlit } from "streamlit-component-lib"
import { useRenderData } from "streamlit-component-lib-react-hooks"
import React, { useState, useEffect } from "react"
import Vertex from "./Vertex"
import { TextField } from "./TextField"
import image from "./assets/ground.png"

export interface VertexData {
  id: string
  name: string
  dir1: string
  dir2: string
  x1: number
  y1: number
  x2: number
  y2: number
}

function equal(vertex1: VertexData, vertex2: VertexData): boolean {
  return (
    vertex1.id === vertex2.id &&
    vertex1.name === vertex2.name &&
    vertex1.dir1 === vertex2.dir1 &&
    vertex1.dir2 === vertex2.dir2 &&
    vertex1.x1 === vertex2.x1 &&
    vertex1.y1 === vertex2.y1 &&
    vertex1.x2 === vertex2.x2 &&
    vertex1.y2 === vertex2.y2
  )
}

function contains(data: VertexData, arr: VertexData[]): boolean {
  return arr.some((otherData) => equal(data, otherData))
}

export function calcVertexId(vertex: VertexData): string {
  return `${vertex.x1}${vertex.y1}${vertex.x2}${vertex.y2}${vertex.name}${vertex.dir1}${vertex.dir2}`
}

const LinePlacementPlot: React.VFC = () => {
  // "useRenderData" returns the renderData passed from Python.
  const renderData = useRenderData()
  const [coordinates, setCoordinates] = useState<VertexData[]>([])
  const [vertices, setVertices] = useState<JSX.Element[]>([])
  const [selectedIndex, setSelectedIndex] = useState<number | undefined>()
  const colors = ["red", "green", "blue", "yellow", "pink"]
  const img = renderData.args["image"]
  const WIDTH = renderData.args["width"]
  const HEIGHT = renderData.args["height"]
  const [key, setKey] = useState<number>(renderData.args["key"])
  const changed = renderData.args["changed"]
  let colorIndex = 0
  const [coordinatesChanged, setCoordinatesChanged] = useState(false)

  function reduceVertexArrays(arr1: VertexData[], arr2: VertexData[]) {
    const resArray: VertexData[] = key === changed ? arr1 : arr2
    return resArray.map((data) => {
      return { ...data, id: calcVertexId(data) }
    })
  }

  let firstClick: [number, number] | null = null

  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      if ((event.ctrlKey || event.metaKey) && event.key === "z") {
        setSelectedIndex(undefined)
        setCoordinates((coords) => {
          const arr = coords.slice(0, -1)
          Streamlit.setComponentValue(arr)
          return arr
        })
        setCoordinatesChanged(true)
      }
      if (event.key === "Enter") {
        setSelectedIndex(undefined)
      }
    }

    window.addEventListener("keydown", handleKeyDown)
  }, [])

  useEffect(() => {
    const renderArgs = () => {
      const args: VertexData[] = renderData.args["lines"]
      console.log(args)
      setCoordinates((coords) => reduceVertexArrays(coords, args))
    }
    renderArgs()
  }, [renderData.args])

  useEffect(() => {
    const updateVertices = () => {
      const verticesArray: JSX.Element[] = []
      for (let i = 0; i < coordinates.length; i++) {
        const data = coordinates[i]
        verticesArray.push(
          <Vertex
            data={data}
            index={i}
            setIndex={setSelectedIndex}
            color={colors[colorIndex]}
            key={`vertex_${data.id}}`}
          />
        )
        colorIndex = (colorIndex + 1) % colors.length
      }
      setVertices(verticesArray)
      if (coordinatesChanged) {
        Streamlit.setComponentValue(coordinates)
        setCoordinatesChanged(false)
      }
    }
    updateVertices()
  }, [coordinates])

  const getClickCoords = (event: {
    target: any
    clientX: number
    clientY: number
  }) => {
    var e = event.target
    var dim = e.getBoundingClientRect()
    var x = event.clientX - dim.left
    var y = event.clientY - dim.top
    return [x, y]
  }

  const click = (event: any) => {
    const [x, y] = getClickCoords(event)
    if (firstClick === null) {
      firstClick = [x, y]
    } else {
      const [x1, y1] = firstClick
      const nextCoord: VertexData = {
        x1: x1,
        y1: y1,
        x2: x,
        y2: y,
        name: `Vertex ${vertices.length}`,
        dir1: "Up",
        dir2: "Down",
        id: `${x1}${y1}${x}${y}${`Vertex ${vertices.length}`}${"Up"}${"Down"}`,
      }
      if (key === changed) setCoordinates((coords) => [...coords, nextCoord])
      setCoordinatesChanged(true)
      colorIndex = (colorIndex + 1) % colors.length
      firstClick = null
    }
  }

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
      }}
    >
      <div
        style={{
          width: WIDTH,
          height: HEIGHT,
          backgroundImage: `url(${img})`,
          backgroundSize: "cover",
          overflow: "hidden",
          position: "relative",
          display: "inline-block",
        }}
        onClick={(event) => {
          if (selectedIndex === undefined) click(event)
        }}
      >
        <svg width={WIDTH} height={HEIGHT}>
          {vertices}
        </svg>
      </div>
      <div>
        {key === changed ? (
          selectedIndex !== undefined ? (
            <TextField
              coords={coordinates[selectedIndex]}
              index={selectedIndex}
              setCoordinates={setCoordinates}
              setCoordinatesChanged={setCoordinatesChanged}
              setIndex={setSelectedIndex}
            ></TextField>
          ) : (
            <div style={{ display: "flex", maxWidth: WIDTH, flexWrap: "wrap" }}>
              {coordinates.map((data, index) => (
                <h4
                  style={{
                    backgroundColor: "#282434",
                    outline: "none",
                    padding: "10px",
                    color: "#ffffff",
                    borderRadius: "5px",
                    border: "none",
                    fontSize: "12px",
                    margin: "4px",
                    boxSizing: "border-box",
                    width: WIDTH / 8,
                    fontFamily: "sans-serif",
                    fontWeight: 10,
                  }}
                  onClick={() => setSelectedIndex(index)}
                  key={`name_${data.id}`}
                >
                  {data.name}
                </h4>
              ))}
            </div>
          )
        ) : (
          <></>
        )}
      </div>
    </div>
  )
}

export default LinePlacementPlot
